.showcase {
	width: 100%;
	height: 93vh;
	position: relative;
    background: url('https://i.ibb.co/vXqDmnh/background.jpg') no-repeat center center/cover;
    
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background: rgba(0, 0, 0, 0.6);
        box-shadow: inset 120px 100px 250px var(--black), inset -120px -100px 250px var(--black);
    }
}

.showcase-top {
	position: relative;
	z-index: 2;
    height: 90px;
    
    img {
        width: 170px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        margin-left: 0;
    }

    a {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(-50%, -50%);
    }
}

.showcase-content {
	position: relative;
	z-index: 2;
	width: 65%;
	margin: auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
    margin-top: 180px;
    
    h1 {
        font-weight: 700;
        font-size: 55px;
        line-height: 1.1;
        margin: 0 0 20px;
    }

    p {
        text-transform: uppercase;
        color: var(--white);
        font-weight: 400;
        font-size: 20px;
        line-height: 1.25;
        margin: 0 0 20px;
    }   
}